<template>
<div v-if="file">
    <nav class="level is-mobile">
        <div class="level-item has-text-centered">
            <div>
                <p class="heading">{{ $t('file.type') }}</p>
                <p class="title is-4">{{ file.extension.toUpperCase() }}</p>
            </div>
        </div>
        <div class="level-item has-text-centered">
            <div>
                <p class="heading">{{ $t('file.size') }}</p>
                <p class="title is-4">{{ file.size | humanBytes }}</p>
            </div>
        </div>
        <div class="level-item has-text-centered">
            <div>
                <p class="heading">{{ $t('file.downloaded') }}</p>
                <p class="title is-4">{{ file.downloadCount }}</p>
            </div>
        </div>
        <div class="level-item has-text-centered">
            <div>
                <p class="heading">{{ $t('file.visited') }}</p>
                <p class="title is-4">{{ file.visitCount }}</p>
            </div>
        </div>
    </nav>

    <hr>
    <h1 class="title is-5 tracking-wide mt-12 text-center">{{ file.name }}</h1>

    <div class="ads mt-10 text-center">
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="9178412838"></Adsense>
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
    </div>

    <div class="text-center" v-show="showDownloadButtons">
        <a
        :href="`${file.url}`"
        class="button is-primary is-rounded mr-4 uppercase font-bold"
        :class="{ 'pointer-events-none': downloaded }"
        target="_blank"
        @click="onFileDownload"
        :disabled="downloaded"
      >{{ $t("file.downloadFile", { num: '1' }) }}</a>
        <a
        :href="`${file.url_s3}`"
        class="button is-rounded is-white uppercase font-bold"
        :class="{ 'pointer-events-none': downloaded }"
        target="_blank"
        @click="onFileDownload"
        :disabled="downloaded"
      >{{ $t("file.downloadFile", { num: '2' }) }}</a>
    </div>

    <div class="ads text-center">
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
    </div>

    <div class="mt-20">
        <h4 class="title title--no-sp is-6">Research: 64% using or considering industry cloud services</h4>

        <p>
            With more specialized applications and tailored services, industry cloud, which targets vertical industries, has won over tech professionals. In a recent TechRepublic Premium survey, 64% of respondents said they either already use industry cloud services or plan to adopt it in the next 12 months. Of respondents, only 18% have no plans to consider or adopt industry cloud services.

Despite the bells and whistles that industry cloud service vendors promote, respondents consider many additional factors. Not surprisingly, most survey respondents based their decisions on operational cost or security/data protection. Speed and agility was another popular consideration for more than half (54%) of respondents; while 44% said implementation cost was an important selection factor. Other factors ranked as important when choosing an industry cloud service included regulatory/industry compliance, vendor industry experience, and whether a vendor provides a turnkey system for a specific industry.
Annual budgets for industry cloud services varied significantly, topping out more than $1 million with some reporting in at less than $10,000.

With 61% of survey respondents citing easy management/administration, and 54% citing secure data protection as positive outcomes, it's no surprise that so many organizations are embracing this type of cloud service. Other industry cloud service benefits recognized by respondents include simpler cloud migration/adoption, more agile business processes, lower cost, and industry-specific turnkey core systems, not to mention simplified regulatory and industry compliance.

However, not all respondents were fond of their industry cloud services. Some (41%) had difficulty integrating industry cloud services with existing business processes, and 37% faced migration and adoption challenges. Further, 31% of respondents cited cost as a negative factor, and 20% cited complex management and administration. A smaller percentage (19%) experienced complex management and administration, outages, poor service, and services not working as expected. Eleven percent stated that their industry cloud services were more difficult to use than expected.
        </p>
    </div>
</div>
</template>

<script>
import axios from 'axios';
import EventBus from '@/eventBus';
import prettyBytes from 'pretty-bytes';

export default {
  data: () => ({
    file: null,
    title: '',
    downloaded: false,
    showDownloadButtons: false,
  }),
  filters: {
    humanBytes(val) {
      return prettyBytes(Number(val));
    },
  },
  mounted() {
    axios
      .get(`/api/files/${this.$route.params.id}`)
      .then(({
        data: file,
      }) => {
        this.file = file;
        this.file.visitCount += 1;
        this.title = `${file.name} - `;

        setTimeout(() => {
          this.showDownloadButtons = true;
        }, 2000);
      })
      .catch((err) => {
        console.log(err);

        if (err.response.status === 500) {
          return EventBus.$emit(
            'error',
            'Server Error: Unable to handle the request now. Please try again later.',
          );
        }

        return this.$router.push('/404');
      });

    // setTimeout(() => {
    //   const ad = document.querySelector('ins.adsbygoogle');
    //   if (ad && ad.innerHTML.replace(/\s/g, '').length === 0) {
    //     this.$router.push({ name: 'ad-blocker' });
    //   }
    // }, 1500);
  },
  methods: {
    onFileDownload() {
      this.file.downloadCount += 1;
      this.downloaded = true;

      axios.post(`/api//download/${this.file.encodedName}`);
    },
  },
  metaInfo() {
    return {
      title: this.title,
    };
  },
};
</script>
